<template>
  <div class="selection-component">
    <select class="selection-show" v-model="selected">
      <option class="selection-list" v-for="(item, index) in selections" :value="item">{{ item.label }}</option>
    </select>
  </div>
</template>
<script>
  export default {
    props: {
      selections: {
        type: Array,
        default: [{
          label: 'test',
          value: 0
        }]
      }
    },
    data() {
      return {
        nowIndex: 0,
        selected:{
          label: '入门版',
          value: 0
        }
      }
    },
    methods:{

    },
    watch: {
      selected () {
        this.$emit('on-change', this.selected)
      }
    },
  }
</script>
<style scoped>
  .selection-component {
    position: relative;
    display: inline-block;
  }

  .selection-show {
    border: 1px solid #e3e3e3;
    padding: 0 20px 0 10px;
    display: inline-block;
    position: relative;
    cursor: pointer;
    height: 25px;
    line-height: 25px;
    border-radius: 3px;
    background: #fff;
  }

  .selection-show .arrow {
    display: inline-block;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-top: 5px solid #e3e3e3;
    width: 0;
    height: 0;
    margin-top: -1px;
    margin-left: 6px;
    margin-right: -14px;
    vertical-align: middle;
  }

  .selection-list {
    display: inline-block;
    position: absolute;
    left: 0;
    top: 25px;
    width: 100%;
    background: #fff;
    border-top: 1px solid #e3e3e3;
    border-bottom: 1px solid #e3e3e3;
    z-index: 5;
  }

  .selection-list li {
    padding: 5px 15px 5px 10px;
    border-left: 1px solid #e3e3e3;
    border-right: 1px solid #e3e3e3;
    cursor: pointer;
    background: #fff;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

  }

  .selection-list li:hover {
    background: #e3e3e3;
  }
</style>



